'use client';

import React, { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { hotelsApi } from '@/lib/hotels';
import { MapPin, Phone, Building2, Radar } from 'lucide-react';

interface HotelItem {
  _id: string;
  organizationName: string;
  contactPerson: string;
  phone: string;
  address: {
    street: string;
    city: string;
    state: string;
    zipCode: string;
    country: string;
  };
  distanceMeters?: number;
}

const NearbyHotelsPage: React.FC = () => {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [hotels, setHotels] = useState<HotelItem[]>([]);
  const [usedGeo, setUsedGeo] = useState(false);
  const [radiusKm, setRadiusKm] = useState<number>(10);

  useEffect(() => {
    const fetchHotels = async () => {
      try {
        setLoading(true);
        const res = await hotelsApi.getNearby(radiusKm);
        setHotels(res.hotels || []);
        setUsedGeo(!!res.usedGeo);
      } catch (e) {
        console.error('Failed to load nearby hotels', e);
      } finally {
        setLoading(false);
      }
    };
    fetchHotels();
  }, [radiusKm]);

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
        <div className="text-center">Loading nearby hotels...</div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Nearby Hotels</h1>
        <p className="mt-2 text-gray-600">Based on your saved location: {user?.address.city}, {user?.address.state}</p>
        <div className="mt-4 flex items-center gap-2">
          <Radar className="h-5 w-5 text-gray-500" />
          <label className="text-sm text-gray-700">Search radius:</label>
          <select
            className="rounded-md border-gray-300 text-sm"
            value={radiusKm}
            onChange={(e) => setRadiusKm(Number(e.target.value))}
          >
            <option value={5}>5 km</option>
            <option value={10}>10 km</option>
            <option value={20}>20 km</option>
            <option value={30}>30 km</option>
            <option value={50}>50 km</option>
          </select>
          {usedGeo ? (
            <span className="ml-2 text-xs text-green-700 bg-green-100 px-2 py-0.5 rounded">Geo-distance enabled</span>
          ) : (
            <span className="ml-2 text-xs text-gray-700 bg-gray-100 px-2 py-0.5 rounded">City/State matching</span>
          )}
        </div>
      </div>

      {hotels.length === 0 ? (
        <div className="text-center py-12">
          <p className="text-gray-500">No hotels found in your area.</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {hotels.map(hotel => (
            <div key={hotel._id} className="bg-white rounded-lg shadow-md p-6">
              <div className="flex items-center mb-3">
                <Building2 className="h-5 w-5 text-gray-500 mr-2" />
                <h3 className="text-lg font-semibold text-gray-900">{hotel.organizationName}</h3>
              </div>
              <div className="text-sm text-gray-600 space-y-2">
                <div className="flex items-start">
                  <MapPin className="h-4 w-4 text-gray-500 mr-2 mt-0.5" />
                  <div>
                    <p>{hotel.address.street}</p>
                    <p>{hotel.address.city}, {hotel.address.state} {hotel.address.zipCode}</p>
                  </div>
                </div>
                <div className="flex items-center">
                  <Phone className="h-4 w-4 text-gray-500 mr-2" />
                  <span>{hotel.phone}</span>
                </div>
                {typeof hotel.distanceMeters === 'number' && (
                  <div className="text-xs text-gray-500">
                    {(hotel.distanceMeters / 1000).toFixed(1)} km away
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default NearbyHotelsPage;
